import { graphql } from 'react-apollo'
import gql from 'graphql-tag'

function SendNotification ({ createPost }) {
  function handleSubmit (event) {
    event.preventDefault()

    const form = event.target

    const formData = new window.FormData(form)
    createPost(formData.get('title'), formData.get('url'))

    form.reset()
  }

  return (
    <form onSubmit={handleSubmit}>
      <h1>Submit</h1>
      <input placeholder='title' name='title' type='text' required />
      <input placeholder='message' name='message' type='text' required />
      <button type='submit'>Submit</button>
      <style jsx>{`
        form {
          border-bottom: 1px solid #ececec;
          padding-bottom: 20px;
          margin-bottom: 20px;
        }
        h1 {
          font-size: 20px;
        }
        input {
          display: block;
          margin-bottom: 10px;
        }
      `}</style>
    </form>
  )
}

const selectPush = gql`
  mutation select_push(
    $logged_in_user: String!, $zip: String!, $title: String!,
    $rep: String!, $send: Boolean!, $platform: String!,
    $state: String!, $radius: Float!, $user: String!,
    $provider: String!, $message: String!, $api_key: String!
  ) {
    select_push(title: $title, url: $url) {

    }
  }
`

export default graphql(selectPush, {
  props: ({ mutate }) => ({
    selectPush: (title, url) =>
      mutate({
        variables: { title, url },
        update: (proxy, { data: { createPost } }) => {
          const data = proxy.readQuery({
            query: allPosts,
            variables: allPostsQueryVars
          })
          proxy.writeQuery({
            query: allPosts,
            data: {
              ...data,
              allPosts: [createPost, ...data.allPosts]
            },
            variables: allPostsQueryVars
          })
        }
      })
  })
})(SendNotification)



